} */

body {
    min-width: 350px;
    max-height: 100vh;
    margin: 0; height: 100%; overflow: hidden
    /* background: #1578d0; */
}

.App {
    text-align: center;
    font-family: "-apple-system", "Lato", "system-ui", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", "Helvetica Neue", sans-serif;
    font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
    line-height: calc(1.3em + (1.5 - 1.2) * ((100vw - 300px)/(1600 - 300)));
    min-width: 350px;
    min-height: 100vh;
    background: #1578d0;
}

.App-main {
    min-width: 350px;
}

#header-content {
    min-width: 350px;
    padding-bottom: 200px;
}

h1 {
    margin-top: 50px;
    font-weight: 400;
    font-size: 200%;
}

h2 {
    font-weight: 700;
    font-size: 300%;
    line-height: 53px;
}

h3 {
    font-weight: 400;
    font-size: 100%;
    line-height: 23px;
}

.App-main {
    padding-bottom: 10%;
    width: 100vw;
    height: 50%;
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    overflow: hidden;
}

.store-links {
    margin-top: 10px;
    margin-left: 20px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    position: relative;
    z-index: 4;
}

.store-icon {
    width: 140px;
    height: 40px;
    cursor: pointer;
}

.app-link {
    display: inline-block;
    margin: 0 1rem 1rem 0;
}

.device_frame {
    position: absolute;
    padding-top: 20px;
    width: 250px;
    margin-left: -125px;
    z-index: 100;
    overflow: hidden;
}

#particles {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}